import React, { useState } from "react";
import { Input, Row, Col, Select, Form, Button } from "antd";
import "flag-icon-css/css/flag-icon.css";

import { postJob } from "../../../utils/http_functions";
import { parseJSON } from "../../../utils/misc";
const { Option } = Select;

export const JobPostPayment = props => {
    const [stripeCheck, setStripeCheck] = useState(false);

    const [successMessage, setSuccessMessage] = useState("");

    const [errMessage, setErrMessage] = useState("");

    const { values, form } = props;

    const { getFieldDecorator } = form;

    const formItemLayout = {
        labelCol: {
            xs: { span: 24 },
            sm: { span: 8 }
        },
        wrapperCol: {
            xs: { span: 24 },
            sm: { span: 16 }
        }
    };

    const tailFormItemLayout = {
        wrapperCol: {
            xs: {
                span: 24,
                offset: 0
            },
            sm: {
                span: 16,
                offset: 8
            }
        }
    };

    // const parseExpirationDate = val => {
    //     val.expDate = val;
    //     if (val && val.length == 5) {
    //         const split = val.split("/");
    //         const mo = split[0];
    //         const yr = split[1];
    //         return [mo, yr];
    //     }
    //     return [];
    // };

    const onDaysChange = val => {
        values.activeDays = val;
    };

    const submitJob = e => {
        e.preventDefault();
        form.validateFieldsAndScroll((err, values) => {
            if (err) {
                console.log("error", err);
                return;
            }
            console.log("values", values);
            setStripeCheck(true);
            console.log("key", process.env.REACT_APP_STRIPE_PUBLISH_KEY);
            window.Stripe.setPublishableKey(
                process.env.REACT_APP_STRIPE_PUBLISH_KEY
            );
            // const ed = parseExpirationDate(form.getFieldValue('expDate'))
            const card = {
                number: values.card,
                cvc: values.cvc,
                exp_month: values.expDate.slice(0, 2),
                exp_year: values.expDate.slice(2, 4)
            };
            console.log(card);
            window.Stripe.createToken(card, (status, response) => {
                if (response.error) {
                    setStripeCheck(false);
                    // this.errors.push(response.error.message);
                    // eslint-disable-next-line
                    console.error(response);
                    const { message } = response.error;
                    console.log(message);
                    setErrMessage(
                        <span>
                            <strong>Payment Error: </strong>
                            {message}
                        </span>
                    );
                } else {
                    const payload = {
                        stripeToken: response.id,
                        ...values
                    };
                    postJob(payload)
                        .then(parseJSON)
                        .then(response => {
                            const { message, id, success } = response;
                            if (success) {
                                setSuccessMessage(
                                    <span>
                                        Success. Your job posting is now visible
                                        at `https://www.gisjobsmap.com/job/
                                        {message.id}-{message.slug}`.
                                    </span>
                                );
                            }
                            setErrMessage("");
                        })
                        .catch(error => {
                            setStripeCheck(false);
                            if (error.response) {
                                setErrMessage(
                                    <span>
                                        <strong>Server Error: </strong>code{" "}
                                        {error.response.status}. Please try
                                        again.
                                    </span>
                                );
                            } else {
                                setErrMessage(
                                    <span>
                                        <strong>Server Error.</strong> Please
                                        try again.
                                    </span>
                                );
                            }
                        });
                }
            });
        });
    };

    const handleExpDateChange = e => {
        e.preventDefault();
        const { value } = e.target;
        const formattedValue = value.slice(0, 2) + "-" + value.slice(2, 4);
        console.log(formattedValue);
        form.setFieldsValue({
            expDate: formattedValue
        });
    };

    return (
        <div style={{ padding: "20px" }}>
            <Row gutter={24} type="flex">
                <Col span={24} style={{ textAlign: "left" }}>
                    <Form
                        {...formItemLayout}
                        labelAlign="left"
                        onSubmit={submitJob}
                    >
                        <Form.Item label={<span>Job Post Duration</span>}>
                            {getFieldDecorator("activeDays", {
                                initialValue: values.activeDays,
                                rules: [
                                    {
                                        required: true,
                                        message:
                                            "Please select a job post duration."
                                    }
                                ]
                            })(
                                <Select
                                    placeholder="Select duration"
                                    onChange={onDaysChange}
                                    style={{ maxWidth: "200px" }}
                                >
                                    {[30, 60, 90, 120].map(item => {
                                        return (
                                            <Option key={item} value={item}>
                                                {item} days
                                            </Option>
                                        );
                                    })}
                                </Select>
                            )}
                        </Form.Item>

                        <Form.Item label={<span>Billing Email</span>}>
                            {getFieldDecorator("email", {
                                initialValue: values.email,
                                rules: [
                                    {
                                        required: true,
                                        message: "Please enter a valid email.",
                                        type: "email"
                                    }
                                ]
                            })(
                                <Input
                                    placeholder="jane.doe@gmail.com"
                                    style={{ maxWidth: "300px" }}
                                />
                            )}
                        </Form.Item>

                        <Form.Item label={<span>Card Number</span>}>
                            {getFieldDecorator("card", {
                                // initialValue: values.email,
                                rules: [
                                    {
                                        required: true,
                                        message:
                                            "Please enter a valid credit card number.",
                                        min: 16
                                    }
                                ]
                            })(
                                <Input
                                    placeholder="XXXXXXXXXXXXXXXX"
                                    style={{ maxWidth: "300px" }}
                                />
                            )}
                        </Form.Item>

                        <Form.Item label={<span>CVC</span>}>
                            {getFieldDecorator("cvc", {
                                // initialValue: values.email,
                                rules: [
                                    {
                                        required: true,
                                        len: 3,
                                        message:
                                            "Please enter a valid 3 digit code."
                                    }
                                ]
                            })(
                                <Input
                                    placeholder="CVC"
                                    style={{ maxWidth: "50px" }}
                                />
                            )}
                        </Form.Item>

                        <Form.Item label={<span>Exp. Date</span>}>
                            {getFieldDecorator("expDate", {
                                initialValue: values.expDate,
                                rules: [
                                    {
                                        required: true,
                                        len: 4,
                                        type: "string",
                                        //  pattern: /^\d{2}[\/]\d{2}$/,
                                        message:
                                            "Please enter a valid expiration date."
                                        //validator: (rule, value, cb) => parseExpirationDate(value).length == 2,
                                    }
                                ]
                            })(
                                <Input
                                    placeholder="MMYY"
                                    style={{ maxWidth: "90px" }}
                                    // value={form.expDate}
                                    onChange={handleExpDateChange}
                                    allowClear
                                />
                            )}
                        </Form.Item>

                        {/* <Form.Item label={null} {...tailFormItemLayout}>
              {getFieldDecorator("agreement", {
                // initialValue: values.email,
                rules: [
                  {
                    required: true,
                    message: "Please agree to the terms.",
                  }
                ]
              })(
                <Checkbox checked={false}>
                  I agree to the <a href="">terms</a>.
                </Checkbox>
              )}
            </Form.Item> */}

                        <Form.Item {...tailFormItemLayout}>
                            <div
                                style={{
                                    lineHeight: "23px",
                                    marginBottom: "10px"
                                }}
                            >
                                You will be charged a one-time{" "}
                                <strong>
                                    {values.activeDays &&
                                        `$${values.activeDays} USD`}
                                </strong>{" "}
                                fee for this job posting. The charge will be
                                billed from{" "}
                                <a
                                    href="http://www.cartoconcepts.com"
                                    target="_blank"
                                    rel="noopener noreferrer"
                                >
                                    Carto Concepts, LLC
                                </a>
                                . You will be emailed a reciept upon successfull
                                payment processing.
                                {/* &nbsp;&nbsp;Payments are securely processed using <a href="https://stripe.com/" target="_blank">Stripe</a>. */}
                            </div>
                            <Button
                                type="primary"
                                htmlType="submit"
                                // loading={isLoading}
                                disabled={stripeCheck}
                                // onClick={submitJob}
                            >
                                Submit Job and Pay
                            </Button>
                        </Form.Item>
                    </Form>
                    <div>
                        {errMessage ? (
                            <div style={{ color: "red" }}>{errMessage}</div>
                        ) : null}
                        {successMessage ? (
                            <div style={{ color: "green" }}>
                                {successMessage}
                            </div>
                        ) : null}
                    </div>
                </Col>
            </Row>
        </div>
    );
};
