import React, { Fragment } from "react";
import { Row, Col, Typography, Tooltip } from "antd";
import "flag-icon-css/css/flag-icon.css";
import "./NearestCity.css";
const { Text } = Typography;

export const NearestCity = ({ near }) => {
    const { properties } = near;
    const displayString =
        properties && properties.name ? (
            <span>Near {`${properties.name}, ${properties.admin1}`}</span>
        ) : (
            ""
        );
    const countryDisplay =
        properties && properties.iso2 ? (
            <Tooltip title={`${properties.timezone} timezone`}>
                {" "}
                <span
                    className={`near-flag flag-icon flag-icon-${properties.iso2.toLowerCase()}`}
                />
            </Tooltip>
        ) : (
            ""
        );

    return (
        <Fragment>
            <Row>
                <Col
                    span={24}
                    style={{
                        textAlign: "center",
                        fontSize: ".95em"
                    }}
                >
                    <Text ellipsis style={{ maxWidth: "250px" }}>
                        {displayString}
                    </Text>
                    &nbsp;&nbsp;{countryDisplay}
                </Col>
            </Row>
        </Fragment>
    );
};
