import React from "react";

import { getRelativeTime } from "../../../utils/misc";

import { Icon, Row, Col, Typography, Tag, Button } from "antd";

const { Text } = Typography;

export const Header = props => {
    const { source, isRemote } = props;

    let relTime = getRelativeTime(props.publishDate).toLowerCase();
    if (props.title) {
        return (
            <Row gutter={24}>
                <Col span={24}>
                    <h1>{props.title}</h1>
                </Col>

                <Col span={24} style={{ marginBottom: "10px" }}>
                    <h2>{props.company}</h2>
                </Col>

                <Col span={24} style={{ marginBottom: "20px" }}>
                    <Text strong>
                        <Icon type="environment-o" /> {props.formattedLocation}
                    </Text>
                    {isRemote ? (
                        <Text strong>
                            &nbsp;/&nbsp;
                            <Icon type="home" />
                            &nbsp;Remote
                        </Text>
                    ) : null}
                    <Text>&nbsp;-&nbsp;posted&nbsp;{relTime}</Text>
                </Col>

                {props.source === "gjm" && props.categories.length && (
                    <Col span={24} style={{ marginBottom: "20px" }}>
                        Categorized as &nbsp;
                        {props.categories.map(item => {
                            return (
                                <Tag key={item.id} color="volcano">
                                    {item.name}
                                </Tag>
                            );
                        })}
                    </Col>
                )}

                <Col span={24}>
                    Tagged with &nbsp;
                    {props.tags.length ? (
                        props.tags.map(item => {
                            return (
                                <Tag key={item.id} color="geekblue">
                                    {item.name}
                                </Tag>
                            );
                        })
                    ) : (
                        <Text type="secondary">No tags found</Text>
                    )}
                </Col>
                {props.url && (
                    <Col span={24} style={{ marginTop: "20px" }}>
                        <a
                            href={props.url}
                            target="_blank"
                            rel="noopener noreferrer"
                        >
                            <Button type="link" size="small">
                                <Icon type="link-o" />
                                {source === "indeed"
                                    ? " View on Indeed"
                                    : " More Information"}
                            </Button>
                        </a>
                    </Col>
                )}
            </Row>
        );
    }
};
