import React, { useEffect, useState, useRef, useMemo, useContext } from "react";
import { connect } from "react-redux";
import { bindActionCreators } from "redux";

import {
    Map,
    TileLayer,
    ZoomControl,
    FeatureGroup,
    CircleMarker,
    GeoJSON,
    AttributionControl
} from "react-leaflet";
import L from "leaflet";
import "leaflet/dist/leaflet.css";
import "./map.css";

import {
    boxQueryToArray,
    shortenBoxUrlParam,
    stringifyQueryParams,
    validateSearches
} from "../../utils/routing";
import { basemapProvider } from "../../config";
import { fetchData } from "../../modules/data";
import { fetchDetail } from "../../modules/detail";

import { toggleHighlightMarker } from "../../modules/highlightMarker";

import { useRouter } from "../../hooks/useRouter";
import { SideBarContext } from "../../contexts/SidebarContext";
import { JobClusters } from "../JobClusters";

delete L.Icon.Default.prototype._getIconUrl;

L.Icon.Default.mergeOptions({
    iconRetinaUrl: require("leaflet/dist/images/marker-icon-2x.png"), // eslint-disable-line global-require
    iconUrl: require("leaflet/dist/images/marker-icon.png"), // eslint-disable-line global-require
    shadowUrl: require("leaflet/dist/images/marker-shadow.png") // eslint-disable-line global-require
});

const initialBounds = [
    [29.63, -116.88],
    [48.22, -76.87]
];

const LeafletMap = ({
    detail,
    data,
    fetchData,
    fetchDetail,
    highlightMarker
}) => {
    const router = useRouter();
    const { match } = router;
    const startBoxQuery = boxQueryToArray(router.query.box);

    const box = startBoxQuery ? startBoxQuery : initialBounds;

    const { isSidebarOpen, isMobile } = useContext(SideBarContext);

    // eslint-disable-next-line no-unused-vars
    const [mapBounds, setMapBounds] = useState(box);

    // eslint-disable-next-line no-unused-vars
    const [crosshairVisible, setCrosshairVisible] = useState([]);

    const [hasJobDetailOnLoad, setHasJobDetailOnLoad] = useState(false);

    const mapRef = useRef(null);
    const jobFeatureGroupRef = useRef(null);

    // const handleGeocodeChange = (value) => {
    //   setGeocodeValue(value)
    //   if (value) {
    //     const params = {
    //       text: value,
    //       iso2: country
    //     };
    //     fetchGeocode(params)
    //   }
    // }
    const { features } = data;

    const onMarkerClick = e => {
        const { slug } = e.target.options;
        fetchDetail(slug);
    };

    // const onMoveStart = e => {
    //     setCrosshairVisible(true);
    // };

    const onMoveEnd = e => {
        const map = e.target;
        console.log("map", map);
        if (map) {
            setCrosshairVisible(false);
            const bounds = map.getBounds();
            var min = bounds.getSouthWest().wrap();
            var max = bounds.getNorthEast().wrap();
            let boundsString = `${min.lng},${min.lat},${max.lng},${max.lat}`;
            boundsString = bounds.toBBoxString();
            const urlBoxParam = shortenBoxUrlParam(boundsString);
            const { query } = router;
            const newQueries = {
                ...validateSearches(query),
                ...{ box: urlBoxParam }
            };

            const search = stringifyQueryParams(newQueries);
            router.push({ search });
            fetchData(newQueries);
        }
    };

    // const setFullExtent = () => {
    //   console.log("set full extent");
    //   if (mapRef && jobFeatureGroupRef.current && features.length) {
    //     const mapBounds = jobFeatureGroupRef.current.leafletElement.getBounds();
    //     if (mapBounds.isValid()) {
    //       mapRef.leafletElement.fitBounds(mapBounds);
    //     }
    //   }
    // };

    // const goToGeocode = () =>{
    //   const { coords } = geocode
    //   if (coords.length) {
    //     mapRef.current.leafletElement.setView(new L.LatLng(coords[1], coords[0]), 11);
    //   }
    // }

    // toggle map window size with sidebar change

    useEffect(() => {
        if (mapRef && mapRef.current) {
            setTimeout(() => {
                mapRef.current.leafletElement.invalidateSize();
            }, 200);
        }
    }, [isSidebarOpen]);

    // useEffect(() => {
    //   const { layer } = router.query
    //   getData(layer, router.query);
    // }, [router.query]);

    // highlight marker on sidebar hover
    useEffect(() => {
        if (mapRef.current) {
            if (highlightMarker.geometry.coordinates) {
                L.popup({
                    closeButton: false,
                    autoPan: false,
                    offset: [0, -15]
                })
                    .setLatLng([
                        highlightMarker.geometry.coordinates[1],
                        highlightMarker.geometry.coordinates[0]
                    ])
                    .setContent(highlightMarker.title)
                    .openOn(mapRef.current.leafletElement);
            } else {
                mapRef.current.leafletElement.closePopup();
            }
        }
    }, [highlightMarker]);

    // memoized to prevent unecessary rerendering
    const jobClustersMemo = useMemo(() => {
        return (
            <JobClusters markerClick={onMarkerClick} mapFeatures={features} />
        );
    }, [features]);

    useEffect(() => {
        if (match.params.layer === "job" && match.params.slug) {
            setHasJobDetailOnLoad(true);
        } else if (mapRef && mapRef.current) {
            mapRef.current.leafletElement.fitBounds(mapBounds);
        }
    }, []);

    useEffect(() => {
        if (
            !detail.isFetching &&
            detail.data.geometry.coordinates &&
            hasJobDetailOnLoad
        ) {
            setHasJobDetailOnLoad(false);
            const { coordinates } = detail.data.geometry;
            mapRef.current.leafletElement.setView(
                [coordinates[1], coordinates[0]],
                10
            );
        }
    }, [detail.isFetching]);

    // memoized to prevent unecessary rerendering
    // const organizationsClustersMemo = useMemo(() => {
    //   return (
    //     <OrganizationClusters
    //       markerClick={onMarkerClick}
    //       mapFeatures={features}
    //     />
    //   );
    // }, [features]);

    //   const geocoderMemo = useMemo(() => {
    //   return (
    //     <Geocoder
    //     value={geocodeValue}
    //     handleSelect={(e) => setGeocode(e)}
    //     handleChange={(e) => handleGeocodeChange(e)}
    //     searching={geocode.isFetching}
    //     options={geocode.results}
    //   />
    //   )
    // }, [geocodeValue, geocode]);

    // const onMapReady = (e) => {
    // console.log('e', e)
    // console.log('t', t)
    // console.log('e', e)
    // console.log('e', e)
    // setTimeout(function () { e.target.invalidateSize() }, 400);
    // e.resize()
    // }

    const mapStyle = {
        position: isMobile ? "fixed" : "relative",
        width: isMobile ? "calc(100vw - 80px)" : "100%",
        left: isMobile ? "80px" : "0",
        height: "calc(100vh - 50px)",
        marginTop: "50px",
        zIndex: "1"
    };

    return (
        <div style={mapStyle}>
            <Map
                className="leaflet-map"
                zoomControl={false}
                onMoveEnd={onMoveEnd}
                // onMoveStart={onMoveStart}
                maxZoom={12}
                zoom={1}
                center={{ lat: 51, lng: 0 }}
                attributionControl={false}
                // bounds={mapBounds}
                boundsOptions={{ padding: [50, 50] }}
                ref={mapRef}
                // whenReady={onMapReady}
                // crs={L.CRS.EPSG4326}
                worldCopyJump
            >
                <AttributionControl
                    position="bottomright"
                    prefix="Built by <a href='http://ian.shi.land' target='_blank'>http://ian.shi.land</a>"
                />
                {/* <div className='crosshair' style={{ visibility: crosshairVisible ? 'visible' : 'hidden' }}>
          <Icon
            type="plus"
          />
        </div> */}

                <TileLayer
                    attribution='&amp;copy <a href="http://osm.org/copyright">OpenStreetMap</a> contributors'
                    url={basemapProvider}
                    // url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png"
                />

                <ZoomControl position="bottomright" />

                <FeatureGroup ref={jobFeatureGroupRef}>
                    {jobClustersMemo}
                </FeatureGroup>

                {data.country &&
                data.country.geom.coordinates &&
                data.country.geom.coordinates.length ? (
                    <GeoJSON
                        data={{
                            type: "Feature",
                            properties: {},
                            geometry: data.country.geom
                        }}
                        className="country-layer"
                        style={() => ({
                            stroke: false,
                            weight: 1,
                            fillOpacity: 0.1
                        })}
                    />
                ) : null}

                {highlightMarker.geometry.coordinates ? (
                    <CircleMarker
                        key={`highlight-layer-${highlightMarker.title}`}
                        center={[
                            highlightMarker.geometry.coordinates[1],
                            highlightMarker.geometry.coordinates[0]
                        ]}
                        stroke
                        strokeColor="#00FFFF"
                        strokeOpacity={0.7}
                        radius={20}
                    />
                ) : null}
            </Map>
        </div>
    );
};

const mapStateToProps = ({ data, highlightMarker, detail }) => ({
    data,
    highlightMarker,
    detail
});

const mapDispatchToProps = dispatch =>
    bindActionCreators(
        {
            fetchData,
            fetchDetail,
            toggleHighlightMarker
        },
        dispatch
    );

export default connect(mapStateToProps, mapDispatchToProps)(LeafletMap);
