import React, { createContext, useState } from "react";
import PropTypes from "prop-types";

export const JobPostContext = createContext();

function JobPostContextProvider({ children }) {
    const [isJobPostOpen, setJobPostOpen] = useState(false);

    return (
        <JobPostContext.Provider
            value={{
                isJobPostOpen,
                setJobPostOpen
            }}
        >
            {children}
        </JobPostContext.Provider>
    );
}

JobPostContextProvider.propTypes = {
    children: PropTypes.any
};

export default JobPostContextProvider;
