import React, { useEffect } from "react";
import { connect } from "react-redux";
import { bindActionCreators } from "redux";
import { Modal, Icon, Typography, Divider } from "antd";

import { useRouter } from "../../hooks/useRouter";
import { fetchDetail, closeDetail } from "../../modules/detail";
import { Header } from "../JobDetail/Header";
import { Share } from "./Share";
import "./style.css";

const { Paragraph, Text } = Typography;

const JobDetail = ({ detail, closeDetail, fetchDetail }) => {
    const router = useRouter();

    const { match } = router;
    const { isFetching, data, slug } = detail;

    const {
        url,
        description,
        formattedLocation,
        dataSource,
        company,
        publishDate,
        tags,
        categories,
        title,
        isRemote
    } = data.properties;

    useEffect(() => {
        if (!slug && match.params.layer === "job" && match.params.slug) {
            fetchDetail(match.params.slug);
        }
    }, []);

    useEffect(() => {
        if (slug) {
            router.push({
                pathname: `/job/${slug}`,
                search: router.location.search
            });
        }
    }, [slug]);

    const closeModal = () => {
        router.push({ pathname: `/`, search: router.location.search });
        closeDetail();
    };

    return (
        <Modal
            title={null}
            visible={slug ? true : false}
            onCancel={() => closeModal()}
            bodyStyle={{ paddingTop: "50px" }}
            footer={null}
            style={{ top: 50 }}
            width="calc(100% - 100px)"
        >
            {isFetching ? (
                <div className="loading-div">
                    <Icon type="loading" /> Loading
                </div>
            ) : (
                <>
                    <Header
                        title={title}
                        company={company}
                        publishDate={publishDate}
                        formattedLocation={formattedLocation}
                        source={dataSource}
                        url={url}
                        tags={tags}
                        categories={categories}
                        isRemote={isRemote}
                    />
                    <Divider />
                    <Share slug={slug} title={title} tags={tags} />

                    <Paragraph className="job-description">
                        <div style={{ margin: "20px 0" }}>
                            <Text strong>Description</Text>
                        </div>
                        {description}
                    </Paragraph>
                </>
            )}
        </Modal>
    );
};

const mapStateToProps = ({ detail }) => ({
    detail
});

const mapDispatchToProps = dispatch =>
    bindActionCreators(
        {
            fetchDetail,
            closeDetail
        },
        dispatch
    );

export default connect(mapStateToProps, mapDispatchToProps)(JobDetail);
