import React, { useState, useMemo } from "react";
import { connect } from "react-redux";
import { bindActionCreators } from "redux";
import { Layout, Menu, Icon } from "antd";
import "flag-icon-css/css/flag-icon.css";

import { fetchGeocodeResults, setGeocodeResults } from "../../modules/geocode";

import { fetchData } from "../../modules/data";

import { AboutModal } from "../AboutModal";
import { PrivacyModal } from "../PrivacyModal";

import "./Navbar.css";

const Navbar = () => {
    const [aboutModalVisible, setAboutModalVisible] = useState(false);
    const [privacyModalVisible, setPrivacyModalVisible] = useState(false);

    const handleMenuClick = e => {
        const { key } = e;
        if (key) {
            if (key === "About") {
                setAboutModalVisible(true);
            } else if (key === "Privacy") {
                setPrivacyModalVisible(true);
            } else if (key.indexOf("twitter") > -1) {
                window.open("https://twitter.com/gisjobsmap");
            }
        }
    };

    // memoized to prevent unecessary rerendering
    const aboutMemo = useMemo(() => {
        return (
            <AboutModal
                visible={aboutModalVisible}
                onClose={() => setAboutModalVisible(false)}
            />
        );
    }, [aboutModalVisible]);

    const privacyMemo = useMemo(() => {
        return (
            <PrivacyModal
                visible={privacyModalVisible}
                onClose={() => setPrivacyModalVisible(false)}
            />
        );
    }, [privacyModalVisible]);

    return (
        <Layout.Header
            className="header"
            style={{
                background: "white",
                padding: "0",
                position: "fixed",
                zIndex: 600,
                width: "100%",
                height: "51px"
            }}
        >
            <div className="logo" />
            <Menu
                theme="light"
                mode="horizontal"
                style={{
                    lineHeight: "64px",
                    height: "50px",
                    width: "65px",
                    float: "right"
                }}
                onClick={e => handleMenuClick(e)}
                selectedKeys={[]}
                key="nav"
            >
                {aboutMemo}
                {privacyMemo}

                <Menu.SubMenu
                    key="submenu"
                    expandIcon={<Icon type="menu" />}
                    style={{
                        lineHeight: "54px",
                        float: "right",
                        height: "49px",
                        paddingLeft: "0px"
                    }}
                    title={
                        <span>
                            <Icon type="menu" style={{ marginRight: "5px" }} />
                        </span>
                    }
                >
                    <Menu.Item
                        key="twitter"
                        className="navMenuItem"
                        style={{ padding: "0 10px !important" }}
                    >
                        <span>
                            <Icon type="twitter-o" color="blue" />
                            Follow Us
                        </span>
                    </Menu.Item>

                    <Menu.Item
                        key="About"
                        className="navMenuItem"
                        style={{ padding: "0 10px !important" }}
                    >
                        <span>
                            <Icon type="question-circle" />
                            About
                        </span>
                    </Menu.Item>

                    <Menu.Item
                        key="Privacy"
                        className="navMenuItem"
                        style={{ padding: "0 10px !important" }}
                    >
                        <span>
                            <Icon type="exclamation-circle" />
                            Privacy Policy
                        </span>
                    </Menu.Item>
                </Menu.SubMenu>
            </Menu>
        </Layout.Header>
    );
};

const mapStateToProps = ({ highlightLayer, data }) => ({
    highlightLayer,
    data
});

const mapDispatchToProps = dispatch =>
    bindActionCreators(
        {
            fetchGeocodeResults,
            setGeocodeResults,
            fetchData
        },
        dispatch
    );

export default connect(mapStateToProps, mapDispatchToProps)(Navbar);
