import React, { useState, useContext, useRef } from "react";
import { connect } from "react-redux";
import { bindActionCreators } from "redux";

import { Modal, Steps, Button, Form } from "antd";
import { fetchDetail } from "../../modules/detail";
import { JobPostContext } from "../../contexts/JobPostContext";

import { JobPostInfo } from "./JobPostInfo";

import { JobPostDetails } from "./JobPostDetails";
import { JobPostLocation } from "./JobPostLocation";
import { JobPostPayment } from "./JobPostPayment";

import "./style.css";

const JobPost = () => {
    const detailFormRef = useRef();

    const locationFormRef = useRef();

    const paymentFormRef = useRef();

    const [detailFormValues, setDetailFormValues] = useState({});

    const [locationFormValues, setLocationFormValues] = useState({});

    const [paymentFormValues, setPaymentFormValues] = useState({});

    const WrappedDetailForm = Form.create({
        name: "detail"
    })(JobPostDetails);

    const WrappedLocationForm = Form.create({
        name: "location"
    })(JobPostLocation);

    const WrappedPaymentForm = Form.create({
        name: "payment"
    })(JobPostPayment);

    const { isJobPostOpen, setJobPostOpen } = useContext(JobPostContext);

    const [current, setCurrent] = useState(0);

    const { Step } = Steps;

    const steps = [
        {
            title: "Info",
            content: <JobPostInfo />
        },
        {
            title: "Job Details",
            content: (
                <WrappedDetailForm
                    ref={detailFormRef}
                    values={detailFormValues}
                />
            )
        },
        {
            title: "Job Location",
            content: (
                <WrappedLocationForm
                    ref={locationFormRef}
                    values={locationFormValues}
                    // values={{ ...detailFormValues, ...locationFormValues }}
                />
            )
        },
        {
            title: "Payment",
            content: (
                <WrappedPaymentForm
                    ref={paymentFormRef}
                    values={{
                        ...detailFormValues,
                        ...locationFormValues,
                        ...paymentFormValues
                    }}
                />
            )
        }
    ];

    const next = () => {
        if (current === 1) {
            detailFormRef.current.validateFieldsAndScroll((err, values) => {
                // if (err) {
                //   console.log("error", err);
                //   return;
                // }
                setCurrent(current + 1);
                setDetailFormValues(values);
            });
        } else if (current === 2) {
            locationFormRef.current.validateFieldsAndScroll((err, values) => {
                // if (err) {
                //   console.log("error", err);
                //   return;
                // }
                setCurrent(current + 1);
                setLocationFormValues(values);
            });
        } else if (current === 3) {
            paymentFormRef.current.validateFieldsAndScroll((err, values) => {
                // if (err) {
                //   console.log("error", err);
                //   return;
                // }
                setCurrent(current + 1);
                setPaymentFormValues(values);
            });
        } else setCurrent(current + 1);
    };

    const prev = () => {
        if (current === 1) {
            detailFormRef.current.validateFieldsAndScroll((err, values) => {
                // if (err) {
                //   console.log("error", err);
                //   return;
                // }
                setCurrent(current - 1);
                setDetailFormValues(values);
            });
        } else if (current === 2) {
            locationFormRef.current.validateFieldsAndScroll((err, values) => {
                // if (err) {
                //   console.log("error", err);
                //   return;
                // }
                setCurrent(current - 1);
                setLocationFormValues(values);
            });
        } else if (current === 3) {
            paymentFormRef.current.validateFieldsAndScroll((err, values) => {
                // if (err) {
                //   console.log("error", err);
                //   return;
                // }
                setCurrent(current - 1);
                setPaymentFormValues(values);
            });
        } else setCurrent(current - 1);
    };

    return (
        <Modal
            visible={isJobPostOpen}
            title="Post a Job"
            onCancel={() => setJobPostOpen(false)}
            footer={null}
            style={{ top: 50, maxWidth: "900px" }}
            width="calc(100% - 100px)"
        >
            <div>
                <Steps current={current} className="steps-post-modal">
                    {steps.map(item => (
                        <Step key={item.title} title={item.title} />
                    ))}
                </Steps>

                <div className="steps-content">{steps[current].content}</div>

                <div className="steps-action">
                    {current < steps.length - 1 && (
                        <Button type="primary" onClick={() => next()}>
                            Next
                        </Button>
                    )}
                    {/* {current === steps.length - 1 && (
            <Button
              type="primary"
              onClick={() => message.success("Processing complete!")}
            >
              Pay Now
            </Button>
          )} */}
                    {current > 0 && (
                        <Button
                            style={{ marginLeft: 8 }}
                            onClick={() => prev()}
                        >
                            Previous
                        </Button>
                    )}
                </div>
            </div>
        </Modal>
    );
};

const mapStateToProps = ({ detail }) => ({
    detail
});

const mapDispatchToProps = dispatch =>
    bindActionCreators(
        {
            fetchDetail
        },
        dispatch
    );

export default connect(mapStateToProps, mapDispatchToProps)(JobPost);
