import React, { useEffect, useState, useRef } from "react";
import { basemapProvider } from "../../../config";
import {
    Icon,
    Tooltip,
    Radio,
    Row,
    Col,
    Form,
    Select,
    AutoComplete
} from "antd";

import { Map, TileLayer, Marker } from "react-leaflet";
import L from "leaflet";
import "leaflet/dist/leaflet.css";
import { parseJSON } from "../../../utils/misc";

import { getGeocode } from "../../../utils/http_functions";

import "flag-icon-css/css/flag-icon.css";
import { countries } from "../../../config";

delete L.Icon.Default.prototype._getIconUrl;

L.Icon.Default.mergeOptions({
    iconRetinaUrl: require("leaflet/dist/images/marker-icon-2x.png"), // eslint-disable-line global-require
    iconUrl: require("leaflet/dist/images/marker-icon.png"), // eslint-disable-line global-require
    shadowUrl: require("leaflet/dist/images/marker-shadow.png") // eslint-disable-line global-require
});

const formItemLayout = {
    layout: "vertical"
};

const getGeocodeOptions = item => {
    const { id, admin1, name } = item.properties;
    const formatted = `${name}, ${admin1}`;

    return (
        <AutoComplete.Option key={id} value={JSON.stringify(item)}>
            {formatted}
        </AutoComplete.Option>
    );
};

export const JobPostLocation = props => {
    const { values, form } = props;

    const mapRef = useRef(null);

    const [geocodeOptions, setGeocodeOptions] = useState([]);

    const { getFieldDecorator } = props.form;

    const setMapCoords = (lat, lon) => {
        if (mapRef.current) {
            if (lat && lon) {
                mapRef.current.leafletElement.setView([lat, lon], 9);
                // console.log('values', values)
                // L.popup({ closeButton: false, autoPan: false, offset: [0, -15] })
                //   .setLatLng(coords)
                //   .setContent(values.location)
                //   // .setContent(values.title)
                //   .openOn(mapRef.current.leafletElement);
            } else {
                mapRef.current.leafletElement.closePopup();
            }
        }
    };

    // useEffect(() => {
    //   setMapCoords();
    // }, [coords]);

    useEffect(() => {
        setMapCoords(values.lat, values.lon);
    }, []);

    const mapStyle = {
        position: "relative",
        width: "100%",
        height: "100%",
        // marginTop: "50px",
        zIndex: "1"
    };

    const mapOverlayStyle = {
        position: "absolute",
        background: "rgba(255, 255, 255, 0.3)",
        height: "100%",
        width: "100%",
        paddingTop: "33%",
        zIndex: 2
    };

    // const onMapClick = e => {
    //   const { latlng } = e;
    //   const { lat, lng } = latlng;
    //   setCoords([lat, lng]);
    //   // form.setFieldsValue({ coordinates: [lat, lng] });
    //   getReverseGeocode({ lat, lon: lng, accuracy: .1 })
    //     .then(parseJSON)
    //     .then(response => {
    //       const { data } = response;
    //       if (data.length) {
    //         const { properties, geometry } = data[data.length - 1];
    //         const { coordinates } = geometry;
    //         form.setFieldsValue({ coordinates });

    //         form.setFieldsValue({ countryCode: properties.iso2.toLowerCase() });
    //         form.setFieldsValue({
    //           city: `${properties.name}, ${properties.admin1}`
    //         });
    //         setGeocodeOptions([data[0]]);

    //       }

    //     })
    //     .catch(error => {
    //       console.error("get reversegeocode error: ", error);
    //       setGeocodeOptions([]);
    //     });
    // };

    const onRemoteChange = e => {
        const { value } = e.target;
        console.log("value", value);
        values.remote = value;
        // if (value == "yes") {
        //   setRemote(true);
        //   value
        // } else {
        //   setRemote(false);
        // }
    };

    const onCountryChange = val => {
        // setCountry(val);
        values.countryCode = val;
        // if (values.formattedLocation) {
        // values.lat = 0;
        // values.lon = 0;
        // values.city = undefined;
        // values.state = undefined;
        // values.formattedLocation = undefined;

        form.setFieldsValue({
            formattedLocation: undefined,
            city: undefined,
            state: undefined,
            lat: undefined,
            lon: undefined
        });
        // }
    };

    const onGeocodeChange = search => {
        values.formattedLocation = search;
        if (search) {
            getGeocode({ text: search, iso2: values.countryCode })
                .then(parseJSON)
                .then(response => {
                    setGeocodeOptions(response.data);
                })
                .catch(error => {
                    console.error("get geocode error: ", error);
                    setGeocodeOptions([]);
                });
        } else {
            // setCoords([]);
            values.lat = 0;
            values.lon = 0;
        }
    };

    const getFormattedLocationHelpText = () => {
        if (!values.countryCode) {
            return "Please select a country first";
        }
        if (values.remote === "yes") {
            return "City not required with 100% remote jobs";
        }
        return "";
    };

    const handleGeocodeSelect = val => {
        // console.log('val', val)
        // console.log('option', option)
        const item = JSON.parse(val);
        const { properties, geometry } = item;
        const { coordinates } = geometry;
        // setCoords([coordinates[1], coordinates[0]]);
        // values.coordinates = [coordinates[1], coordinates[0]]
        const lat = coordinates[1];
        const lon = coordinates[0];
        values.lat = lat;
        values.lon = lon;
        values.city = properties.name;
        values.state = properties.admin1;
        values.formattedLocation = `${properties.name}, ${properties.admin1}`;
        setMapCoords(lat, lon);
        // if (!values.countryCode.length) {
        //   form.setFieldsValue({ countryCode: iso2.toLowerCase() });
        // }
        form.setFieldsValue({
            formattedLocation: `${properties.name}, ${properties.admin1}`,
            city: properties.name,
            state: properties.admin1,
            lat,
            lon
        });
        // form.setFieldsValue({ lat: values.lat });
        // form.setFieldsValue({ lon: values.lon });
    };

    return (
        <div style={{ padding: "20px" }}>
            <Row gutter={24} type="flex">
                <Col span={12} style={{ textAlign: "left" }}>
                    <Form {...formItemLayout} labelAlign="left" colon>
                        <Form.Item label="Country" hasFeedback>
                            {getFieldDecorator("countryCode", {
                                initialValue: values.countryCode,
                                rules: [
                                    {
                                        required: true,
                                        message: "Please select a country",
                                        min: 2
                                    }
                                ]
                            })(
                                <Select
                                    showSearch
                                    onChange={onCountryChange}
                                    style={{ maxWidth: "200px" }}
                                >
                                    {Object.keys(countries).map(key => {
                                        return (
                                            <Select.Option
                                                key={key}
                                                value={key}
                                            >
                                                <span
                                                    className={`flag-icon flag-icon-${key}`}
                                                />{" "}
                                                {countries[key]}
                                            </Select.Option>
                                        );
                                    })}
                                </Select>
                            )}
                        </Form.Item>

                        <Form.Item
                            label={
                                <span>
                                    100% Remote&nbsp;
                                    <Tooltip title="If 'Yes' is selected, the position will be displayed on the list for all areas of the selected country.">
                                        <Icon type="question-circle-o" />
                                    </Tooltip>
                                </span>
                            }
                        >
                            {getFieldDecorator("remote", {
                                initialValue: values.remote ? values.remote : 0
                            })(
                                <Radio.Group onChange={onRemoteChange}>
                                    <Radio.Button value={0}>No</Radio.Button>
                                    <Radio.Button value={1}>Yes</Radio.Button>
                                    {/* <Radio.Button value="possible">Possible</Radio.Button> */}
                                </Radio.Group>
                            )}
                        </Form.Item>
                        {values.remote !== 1 ? (
                            <Form.Item
                                label="City"
                                help={getFormattedLocationHelpText()}
                                // help={remote ? "" : "Optionally select location by clicking the map"}
                            >
                                {getFieldDecorator("formattedLocation", {
                                    initialValue: values.formattedLocation,
                                    rules: [
                                        {
                                            required: values.remote !== 1,
                                            message: "Please enter a valid city"
                                        }
                                    ]
                                })(
                                    <AutoComplete
                                        allowClear
                                        // value={values.formattedLocation}
                                        // className="geocoder-autocomplete"
                                        // disabled={values.remote === "yes" || remote || !values.country || !country}
                                        disabled={!values.countryCode}
                                        placeholder="Type a City..."
                                        filterOption={false}
                                        optionLabelProp="text"
                                        //  style={{ width: '400px' }}
                                        dataSource={geocodeOptions.map(opt =>
                                            getGeocodeOptions(opt)
                                        )}
                                        onSelect={handleGeocodeSelect}
                                        onChange={onGeocodeChange}
                                    />
                                )}
                            </Form.Item>
                        ) : null}

                        <Form.Item style={{ display: "none" }}>
                            {getFieldDecorator("lat", {
                                initialValue: values.lat
                            })(<div />)}
                        </Form.Item>
                        <Form.Item style={{ display: "none" }}>
                            {getFieldDecorator("lon", {
                                initialValue: values.lon
                            })(<div />)}
                        </Form.Item>
                        <Form.Item style={{ display: "none" }}>
                            {getFieldDecorator("city", {
                                initialValue: values.city
                            })(<div />)}
                        </Form.Item>
                        <Form.Item style={{ display: "none" }}>
                            {getFieldDecorator("state", {
                                initialValue: values.state
                            })(<div />)}
                        </Form.Item>
                    </Form>
                </Col>
                <Col span={12}>
                    <div style={mapOverlayStyle} />
                    <div style={mapStyle}>
                        <Map
                            className="leaflet-map"
                            zoomControl={false}
                            maxZoom={12}
                            zoom={1}
                            center={{ lat: 50, lng: 0 }}
                            // onClick={onMapClick}
                            ref={mapRef}
                        >
                            <TileLayer
                                attribution='&amp;copy <a href="http://osm.org/copyright">OpenStreetMap</a> contributors'
                                url={basemapProvider}
                            />

                            {/* {(values.remote !== "yes" || !remote) && (
                <ZoomControl position="bottomright" />
              )} */}

                            {values.lat && values.lon ? (
                                <Marker
                                    key={`highlight-layer-title`}
                                    position={[values.lat, values.lon]}
                                    stroke
                                    strokeColor="#00FFFF"
                                    strokeOpacity={0.7}
                                    radius={20}
                                />
                            ) : null}
                        </Map>
                    </div>
                </Col>
            </Row>
        </div>
    );
};
