import React, { Fragment } from "react";

import { Row, Col, List, Icon, Typography, Tag, Tooltip } from "antd";

import { getRelativeTime } from "../../utils/misc";

const { Text } = Typography;

const CategoriesRow = ({ cat }) => (
    <div style={{ margin: "5px 0" }}>
        {cat.map(c => {
            return (
                <Tag key={c.id} color="blue">
                    {c.name}
                </Tag>
            );
        })}
    </div>
);

export const ListItemSponsored = props => {
    const { properties, geometry } = props.item;
    const { categories } = properties;

    const relTime = properties.publishDate
        ? getRelativeTime(properties.publishDate)
        : null;

    return (
        <Fragment>
            <List.Item.Meta
                // avatar={properties.logo ? <Avatar src={properties.logo} /> : null}
                title={
                    <Row>
                        <Col span={14}>
                            <Text ellipsis style={{ maxWidth: "200px" }}>
                                {properties.title}
                            </Text>
                        </Col>
                        <Col
                            span={10}
                            style={{
                                fontSize: "12px",
                                textAlign: "right",
                                color:
                                    relTime.toLowerCase() === "today"
                                        ? "#0066ff"
                                        : "rgba(0, 0, 0, 0.45)"
                            }}
                        >
                            {relTime}
                        </Col>
                    </Row>
                }
                description={
                    <Fragment>
                        <Row>
                            <Col span={14}>
                                <Text ellipsis style={{ maxWidth: "200px" }}>
                                    {properties.company}
                                </Text>
                            </Col>
                            <Col
                                span={10}
                                style={{ fontSize: "12px", textAlign: "right" }}
                            >
                                <Text ellipsis style={{ maxWidth: "130px" }}>
                                    {properties.isRemote ? (
                                        <span>
                                            <Tooltip title="Remote Job">
                                                <Icon type="home" /> &nbsp;
                                                Remote
                                            </Tooltip>
                                        </span>
                                    ) : null}
                                    {geometry && geometry.coordinates ? (
                                        <span>
                                            <Icon type="environment-o" /> &nbsp;
                                            {properties.formattedLocation}
                                        </span>
                                    ) : null}
                                </Text>
                            </Col>
                        </Row>
                        <Row>
                            <CategoriesRow cat={categories} />
                        </Row>
                        <Row>
                            <div
                                style={{
                                    textAlign: "center",
                                    fontSize: "x-small"
                                }}
                            >
                                Sponsored Listing
                            </div>
                        </Row>
                    </Fragment>
                }
            />
        </Fragment>
    );
};
