export const SET_MAPBOUNDS_REQUEST = "mapbounds/SET_MAPBOUNDS_REQUEST";
export const RESET_MAPBOUNDS_REQUEST = "mapbounds/RESET_MAPBOUNDS_REQUEST";

const initialState = {
    bounds: [
        [-74.8471, 40.0991],
        [-73.504, 41.3809]
    ]
};

// reducer
export default (state = initialState, action) => {
    switch (action.type) {
        case SET_MAPBOUNDS_REQUEST:
            return {
                ...state,
                bounds: action.payload
            };

        case RESET_MAPBOUNDS_REQUEST:
            return initialState;
        default:
            return state;
    }
};

// action
export const updateMapBounds = bounds => {
    return dispatch => {
        if (bounds) {
            dispatch({
                type: SET_MAPBOUNDS_REQUEST,
                payload: bounds
            });
        } else {
            dispatch({
                type: RESET_MAPBOUNDS_REQUEST
            });
        }
    };
};
