import React, { Fragment } from "react";

import { Row, Col, List, Icon, Typography, Tooltip } from "antd";

import { getRelativeTime } from "../../utils/misc";

const { Text } = Typography;

export const ListItem = props => {
    const { properties, geometry } = props.item;

    const relTime = properties.publishDate
        ? getRelativeTime(properties.publishDate)
        : null;

    // Indeed job source
    return (
        <Fragment>
            <List.Item.Meta
                // avatar={properties.logo ? <Avatar src={properties.logo} /> : null}
                title={
                    <Row>
                        <Col span={14}>
                            <Text ellipsis style={{ maxWidth: "200px" }}>
                                {properties.title}
                            </Text>
                        </Col>
                        <Col
                            span={10}
                            style={{
                                fontSize: "12px",
                                textAlign: "right",
                                color:
                                    relTime.toLowerCase() === "today"
                                        ? "#0066ff"
                                        : "rgba(0, 0, 0, 0.45)"
                            }}
                        >
                            {relTime}
                        </Col>
                    </Row>
                }
                description={
                    <Row>
                        <Col span={14}>
                            <Text ellipsis style={{ maxWidth: "200px" }}>
                                {properties.company}
                            </Text>
                        </Col>
                        <Col
                            span={10}
                            style={{ fontSize: "12px", textAlign: "right" }}
                        >
                            <Text ellipsis style={{ maxWidth: "130px" }}>
                                {properties.isRemote ? (
                                    <span>
                                        <Tooltip title="Remote Job">
                                            <Icon
                                                type="home"
                                                style={{ color: "#FC7307" }}
                                            />{" "}
                                            &nbsp;
                                        </Tooltip>
                                    </span>
                                ) : null}
                                {geometry.coordinates ? (
                                    <span>
                                        <Icon type="environment-o" /> &nbsp;
                                        {properties.formattedLocation}
                                    </span>
                                ) : null}
                            </Text>
                        </Col>
                    </Row>
                }
            />
        </Fragment>
    );
};
